  src: url("../fonts/poppins-400.woff2") format("woff2"), url("../fonts/poppins-400.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Poppins-700";
  font-display: swap;
  src: url("../fonts/poppins-700.woff2") format("woff2"), url("../fonts/poppins-700.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Poppins-500";
  font-display: swap;
  src: url("../fonts/poppins-500.woff2") format("woff2"), url("../fonts/poppins-500.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 150%;
  font-family: "Poppins-400", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #fffcf5;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .flex {
    flex-direction: column;
    justify-content: center;
  }
}
.flex__img {
  width: 50%;
}
.flex__img svg {
  width: 100%;
  height: auto;
}
@media (max-width: 767px) {
  .flex__img {
    width: 100%;
    max-width: 300px;
  }
}
.flex__content {
  width: 44%;
  text-align: left;
}
@media (max-width: 767px) {
  .flex__content {
    width: 100%;
    text-align: center;
    margin-bottom: 30px;
  }
}

.section {
  margin-bottom: 115px;
}
@media (max-width: 1023px) {
  .section {
    margin-bottom: 60px;
  }
}
@media (max-width: 767px) {
  .section {
    margin-bottom: 30px;
  }
}

.rouble {
  /* stylelint-disable */
  font-weight: 400 !important;
  /* stylelint-enable */
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 114px;
}
.container--md {
  padding: 0 190px;
}
.container--sm {
  padding: 0 205px;
}
@media (max-width: 1279px) {
  .container {
    padding: 0 70px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  z-index: 5;
  padding-top: 35px;
}
@media (max-width: 767px) {
  .header {
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
@media (max-width: 1023px) {
  .header__wrapper {
    justify-content: space-between;
  }
}
.header__logo {
  margin-top: 5px;
}
@media (max-width: 1023px) {
  .header__logo {
    margin-top: 0;
  }
}
.header.is-open {
  position: fixed;
  height: 100vh;
  width: 100%;
  overflow: scroll;
  border-bottom: none;
  background-color: #f7eddb;
}
.header.is-open .main-nav__list {
  margin: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding-top: 60px;
  width: 100%;
  align-items: flex-start;
}
.header.is-open .main-nav__item {
  width: 100%;
  margin-bottom: 20px;
}
.header.is-open .main-nav__toggle::before, .header.is-open .main-nav__toggle::after {
  content: "";
  position: absolute;
  width: 40px;
  height: 3px;
  background-color: #fe9563;
  box-shadow: none;
  top: 16px;
  left: 1px;
}
@media (max-width: 767px) {
  .header.is-open .main-nav__toggle::before, .header.is-open .main-nav__toggle::after {
    width: 28px;
    height: 2px;
    top: 10px;
  }
}
.header.is-open .main-nav__toggle::before {
  transform: rotate(45deg);
}
.header.is-open .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 1023px) {
  .header.is-open .container {
    height: 100%;
  }
  .header.is-open .header__wrapper {
    flex-direction: column;
    height: 100%;
    padding-bottom: 30px;
    overflow: scroll;
    flex-wrap: nowrap;
    align-items: flex-start;
    justify-content: flex-start;
  }
}

.main-nav__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  margin-left: 85px;
  padding: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .main-nav__list {
    display: none;
  }
}
.main-nav__item {
  margin-right: 27px;
}
.main-nav__item:last-child {
  margin-right: 0;
}
.main-nav__item .btn {
  padding: 5px 20px;
}
.main-nav__link {
  color: #000000;
  position: relative;
  transition: opacity 0.3s ease;
}
.main-nav__link svg {
  position: absolute;
  top: 7px;
  right: -10px;
}
.main-nav__link.is-active {
  opacity: 0.7;
}
@media (hover: hover) {
  .main-nav__link:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.main-nav__link.focus-visible:focus {
  opacity: 0.7;
}
@media (max-width: 1023px) {
  .main-nav__link {
    font-size: 16px;
  }
}

.main-nav__toggle {
  display: none;
}
@media (max-width: 1023px) {
  .main-nav__toggle {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    border: 0;
    background-color: transparent;
    padding: 0;
    margin: 0;
    width: 42px;
    height: 40px;
    cursor: pointer;
  }
  .main-nav__toggle::after {
    content: "";
    position: absolute;
    width: 40px;
    height: 3px;
    background-color: #fe9563;
    top: 8px;
    left: 1px;
    border-radius: 2px;
    box-shadow: 0 9px 0 0 #fe9563, 0 18px 0 0 #fe9563;
  }
}
@media (max-width: 767px) {
  .main-nav__toggle {
    width: 28px;
    height: 24px;
  }
  .main-nav__toggle::after {
    width: 28px;
    height: 2px;
    top: 5px;
    left: 0;
    box-shadow: 0 5px 0 0 #fe9563, 0 10px 0 0 #fe9563;
  }
}

.footer__logo {
  display: block;
}
.footer__top {
  padding-bottom: 40px;
}
.footer__top .container {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
@media (max-width: 1023px) {
  .footer__top .container {
    flex-direction: column;
  }
}
.footer__top .col {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.footer__top .col--1 {
  width: 30%;
  margin-right: 40px;
}
@media (max-width: 1023px) {
  .footer__top .col--1 {
    width: 100%;
    margin-right: 0;
    margin-bottom: 30px;
  }
}
.footer__top .col__list {
  display: flex;
  margin-bottom: 45px;
}
@media (max-width: 767px) {
  .footer__top .col__list {
    flex-wrap: wrap;
  }
}
.footer__top .col__item .title {
  font-size: 15px;
}
.footer__top .col__item .text {
  margin-bottom: 7px;
  font-size: 14px;
  font-weight: 400;
}
@media (max-width: 767px) {
  .footer__top .col__item {
    width: 40%;
  }
}
.footer__top .col__item:not(:last-child) {
  margin-right: 65px;
}
@media (max-width: 767px) {
  .footer__top .col__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
  }
}
.footer__link {
  display: block;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .footer__link:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.footer__link.focus-visible:focus {
  opacity: 0.7;
}
.footer__bottom {
  padding: 20px 0;
  background-color: #f7eddb;
}
.footer__bottom .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  .footer__bottom .container {
    flex-wrap: wrap;
  }
}
@media (max-width: 767px) {
  .footer__bottom .container {
    justify-content: center;
  }
}
.footer__rights.text {
  font-size: 14px;
  font-weight: 400;
}
@media (max-width: 1023px) {
  .footer__rights.text {
    margin: 0 auto;
    margin-bottom: 15px;
    text-align: center;
    width: 80%;
  }
}
.footer__contacts .footer__link {
  display: inline-flex;
  position: relative;
  padding-left: 25px;
  font-weight: 400;
}
.footer__contacts .footer__link svg {
  position: absolute;
  top: 0;
  left: 0;
}
.footer__mail {
  margin-right: 7px;
}
@media (max-width: 1023px) {
  .footer .form {
    width: 70%;
  }
}
@media (max-width: 767px) {
  .footer .form {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .footer .social {
    margin-bottom: 15px;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .modal__close-btn:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
.modal__close-btn.focus-visible:focus {
  opacity: 0.5;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.btn {
