@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Poppins-400";

  font-display: swap;
  src:
    url("../fonts/poppins-400.woff2") format("woff2"),
    url("../fonts/poppins-400.woff") format("woff");
}

